:root {
  /* LETTER */
  --documentHeight: 216mm;
  --documentWidth: 279mm;

  /* A4 */
  /*
  --documentHeight: 210mm;
  --documentWidth: 297mm;
  */
}

@media print {
  @page {
    size: letter landscape;
  }

  .Settings {
    display: none;
  }

  .Page {
    height: 100vh;
    /* fill the print page so content can be bottom-aligned  */
  }
}

.Page {
  overflow: hidden;
}

.PageContent {
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;

  align-items: center;

  position: relative;
}

.Row {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}